body {
  background-color: rgb(250, 250,250);
}

.container {
  display: flex;
  margin-left: 5%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.image {
  width: 500px;
  /* margin-top: 10px; */
  margin-bottom: 50px;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}